@import url('https://fonts.googleapis.com/css2?family=Angkor&family=Battambang&family=Bayon&family=Bokor&family=Chenla&family=Content&family=Dangrek&family=Fasthand&family=Freehand&family=Great+Vibes&family=Hanuman&family=Khmer&family=Koh+Santepheap&family=Koulen&family=Metal&family=Moul&family=Moulpali&family=Nokora&family=Odor+Mean+Chey&family=Poppins&family=Preahvihear&family=Raleway:wght@300;500&family=Siemreap&family=Suwannaphum&family=Taprom&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: "suwannaphum", sans-serif;
    scroll-behavior: smooth;
    -webkit-tap-highlight-color: transparent;
  }
  h1 {
    font-family: "dangrek", sans-serif;
    scroll-behavior: smooth;
    -webkit-tap-highlight-color: transparent;
  }
  body {
    @apply light-mode dark:dark-mode select-none;
  }
  .social-link > a {
    @apply mx-3 md:mx-5 md:hover:opacity-70;
  }
}

@layer components {
  .dark-mode {
    @apply bg-[rgb(13,17,23)] text-slate-300;
  }
  .light-mode {
    @apply bg-[#E4F0FA] text-black;
  }
  .t-g {
    @apply inline-block text-transparent bg-clip-text bg-gradient-to-r from-[#4ca5ff] to-[#b673f8];
  }
  .t-g2 {
    @apply inline-block text-transparent bg-clip-text bg-gradient-to-r from-[#0f2027] to-[#2c5364];
  }
  .bg-primary {
    @apply bg-gradient-to-r from-cyan-500 to-blue-500;
  }
  button {
    @apply active:scale-150 duration-100 
    font-battambang text-xl px-5 py-2 
    rounded-xl bg-primary text-white;
  }
  .selection {
    @apply text-xl font-medium sm:mx-5 text-center mx-1
    dark:bg-[#1E2329] bg-[#F4F6F8];
  }
  .elise-light {
    @apply shadow-[-6px_-6px_6px_0_rgba(255,255,255,0.50),6px_6px_10px_0_rgba(109,129,161,0.25)];
  }
  .elise-night {
    @apply shadow-[-1px_-1px_100px_1px_rgba(14,16,19,1),1px_1px_100px_1px_rgba(65,70,75,1)];
  }
}